<template>

      <div class="myarticle">
        <div class="add" @click="add">
          <i class="el-icon-plus"  style=""></i>
        </div>
        <div class="card" v-for="(i,index) in myartdata">
          <img :src="JSON.parse(i.images)[0]"/>
          <p>{{i.name}}</p>
          <p>{{ i.price }}</p>
        </div>
      </div>
  </template>
  
  <script>
    export default {
      computed:{
        
      },
      data(){
        return{
          myartdata:[],
        }
      },
      methods:{
        add(){
          this.$router.push('/add')
        }
      },
      mounted(){
        let userid=this.$route.params.user
        console.log(userid);
        this.$api.getmyart({userid:userid}).then(res=>{
          console.log(res.data.data);
          this.myartdata=res.data.data
          
        })
      }
    }
  </script>
  <style lang="less" scoped>
    .el-icon-plus{
      font-size: 60px;line-height: 300px;
    }
    .myarticle{
      // height: 100%;
      display: flex;
      flex-wrap: wrap;
      // justify-content: center;
      padding: 10px;
    }
     .add{
      border: dotted 2px;
      text-align: center;
      cursor:pointer;
      --n:4;
      --gap: calc((100% - 204px * var(--n)) / var(--n) /2);
      width: 200px;
      height: 300px;
      border-radius: 10px;
      background: rgb(255, 255, 255);
      margin: 10px var(--gap);
     }
     .add:hover{
      border: dotted greenyellow 2px;
      .el-icon-plus{
        color: greenyellow;
      }
     }
     .card{
      // width: 204px;
      // height: 304px;
      // border-radius: 10px;
      // background: rgb(255, 255, 255);
      // margin: 10px;
      --n:4;
      // --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
      --gap: calc((100% - 204px * var(--n)) / var(--n) /2);
      width: 204px;
      height: 304px;
      border-radius: 10px;
      background: rgb(255, 255, 255);
      margin: 10px var(--gap);
    }
    .card:hover{
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      cursor:pointer
    }
    img{
      width: 100%;height: 260px;object-fit: cover;border-radius: 10px 10px 0 0;
    }
  </style>
  